.btn-s1 {
  padding: 0.65rem 1.50rem;
  min-width: 124px;
  font-size: 1.10rem;
  font-weight: 700;
  letter-spacing: 1px;
  border-radius: 34px;
  text-align: center;

  &.btn-lg {
    padding: 0.75rem 2.1rem;
    font-size: 1.5rem;
  }
  &.btn-sm {
    padding: 0.50rem 1.5rem;
    font-size: 0.95rem;
  }

  &.bg-blue {
    color: #ffffff !important;
    border-color: $blue;
    background-color: $blue;

    &:hover, &:focus, &:active {
      border-color: $blue2 !important;
      background-color: $blue2 !important;
    }
  }
  &.bg-white {
    color: $blue !important;
    border-color: #ffffff;
    background-color: #ffffff;

    &:hover, &:focus, &:active {
      border-color: #e8e4e4 !important;
      background-color: #e8e4e4 !important;
    }
  }
  &.outline-blue {
    color: $blue !important;
    border-color: $blue;
    background-color: transparent !important;

    &:hover, &:focus, &:active {
      color: $blue2 !important;
      border-color: $blue2 !important;
    }
  }
  &.outline-white {
    color: #ffffff !important;
    border-color: #ffffff;
    background-color: transparent !important;

    &:hover, &:focus, &:active {
      color: #eae6e6 !important;
      border-color: #eae6e6 !important;
    }
  }
  &.no-border {
    border: 0 !important;
  }
}
